import {WebAPI} from './web-api';
import {TaskQueue, inject} from 'aurelia-framework';
import {LogManager} from 'aurelia-framework';
import moment from 'moment';
import {SesStorage} from './sesStorage';

let log = LogManager.getLogger('contact-list');

@inject(WebAPI, SesStorage, TaskQueue)
export class ContactList {
  constructor(api, storage, que) {
    log.info('constructor!');
    this.api = api;
    this.storage = storage;
    this.TaskQueue = que;

    this.contacts = [];
    this.activePage = 1;
    this.maxPage = 1;
    this.lineCount = 10;

    this.onCreating = false;
    this.selectedIdx = -1;
    this.dispLoading = false;

    this.sortContactCd = 'sort';
    this.selectedSortItem = null;

    this.dispSearchPanel = false;
    this.srchContactName = null;
    this.srchActiveFlg = false;
    this.condContactName = null;
    this.condActiveFlg = false;
  }

  showList() {
    let orderClass = null;
    let orderItem = null;

    if (this.selectedSortItem) {
      orderItem = this.selectedSortItem.attr('name');
      orderClass = this.selectedSortItem.text() === 'arrow_downward' ? 1 : 2;
      if (orderItem === 'MCNTCT_ContactName' || orderItem === 'MCNTCT_UpdateEmployeeCD') {
        orderClass += 2;
      }
      this.storage.set('orderClass', orderClass);
      this.storage.set('orderItem', orderItem);
    } else {
      orderItem = this.storage.get('orderItem');
      orderClass = this.storage.get('orderClass');
    }
    this.dispLoading = true;
    this.api.getContactList(
      this.activePage,
      this.lineCount,
      orderClass,
      orderItem,
      this.condContactName,
      this.condActiveFlg
    ).then(contacts => {
      log.info('Success!');
      log.info(contacts);
      this.contacts = contacts.map((e) => {
        e.selected = false;
        e.displayUpdated = moment(e.MCNTCT_UpdateDatetime).format('YYYY/MM/DD HH:mm:ss');
        return e;
      });
      let allLineCount = contacts[0].LineCount;
      this.maxPage = Math.floor(allLineCount / this.lineCount);
      if (allLineCount % this.lineCount !== 0) {
        this.maxPage += 1;
      }

      this.TaskQueue.queueMicroTask(this.restoreSortOption());

      this.dispLoading = false;
    }).catch(error => {
      log.info('Error!');
      log.info(error);
      this.dispLoading = false;
    });
  }

  restoreSortOption() {
    return () => {
      if (this.selectedSortItem === null) {
        let oc = this.storage.get('orderClass');
        let oi = this.storage.get('orderItem');
        if (oc && oi) {
          let te = $("i[name='" + oi + "']");
          if (te) {
            if (parseInt(oc, 10) % 2 === 0) {
              log.info('set arrow_upward');
              te.text('arrow_upward');
            } else {
              log.info('set arrow_downward');
              te.text('arrow_downward');
            }
            this.selectedSortItem = te;
            log.info('oc=' + oc);
            log.info('oi=' + oi);
            log.info(te);
          }
        }
      }
    };
  }

  created() {
    this.onCreating = true;
    log.info('created!');
    this.showList();
  }

  onPageChanged(e) {
    log.info(`page changed ${e.detail}`);
    if (this.onCreating) {
      this.onCreating = false;
    } else {
      this.activePage = e.detail;
      this.showList();
    }
    if (this.selectedIdx >= 0) {
      this.contacts[this.selectedIdx].selected = false;
      this.selectedIdx = -1;
    }
  }

  onClickRow(idx) {
    log.info(idx);
    if (this.selectedIdx === idx) {
      this.contacts[this.selectedIdx].selected = false;
      this.selectedIdx = -1;
    } else {
      if (this.selectedIdx >= 0) {
        this.contacts[this.selectedIdx].selected = false;
      }
      this.contacts[idx].selected = true;
      this.selectedIdx = idx;
    }
  }

  openDelModal() {
    this.delmdl.open();
  }

  onDelAgree() {
    log.info('del-agree');
    this.dispLoading = true;
    let body = [{
      'contactCD': this.contacts[this.selectedIdx].MCNTCT_ContactCD
    }];
    this.api.delContact(body).then(contacts => {
      log.info('Success!');
      log.info(contacts);
      this.contacts[this.selectedIdx].selected = false;
      this.selectedIdx = -1;
      this.dispLoading = false;
      this.showList();
    }).catch(error => {
      log.info('Error!');
      log.info(error);
      this.dispLoading = false;
    });
  }

  sortClick(e) {
    let targ = $(e.target);
    let elem = null;
    if (targ && targ.prop('tagName').toUpperCase() === 'I') {
      elem = targ;
    } else if (targ.find('i') && targ.find('i').prop('tagName').toUpperCase() === 'I') {
      elem = targ.find('i');
    } else {
      return;
    }

    if (this.selectedSortItem === null) {
      elem.text('arrow_downward');
      this.selectedSortItem = elem;
      this.showList();
    } else if (this.selectedSortItem.attr('name') === elem.attr('name')) {
      if (elem.text() === 'arrow_downward') {
        elem.text('arrow_upward');
      } else {
        elem.text('arrow_downward');
      }
      this.showList();
    } else {
      this.selectedSortItem.text('sort');
      elem.text('arrow_downward');
      this.selectedSortItem = elem;
      this.showList();
    }

    log.info(elem);
    log.info(elem.attr('name'));
  }

  openSearchPanel() {
    if (this.dispSearchPanel) {
      this.dispSearchPanel = false;
    } else {
      this.srchContactName = this.condContactName;
      this.srchActiveFlg = this.condActiveFlg;
      this.dispSearchPanel = true;
    }
  }

  changeSearchCondition() {
    let strContactName = this.srchContactName ? this.srchContactName : '';
    if (strContactName.trim().length === 0) {
      this.condContactName = null;
    } else {
      this.condContactName = strContactName.trim();
    }
    this.condActiveFlg = this.srchActiveFlg;
    this.dispSearchPanel = false;
    this.showList();
  }
}
